import React from 'react';
import Types from 'prop-types';
import classNames from 'classnames';
import CSSTransition from 'react-transition-group/CSSTransition';
import './Slider.less';

export const EXIT_ANIMATION = 500;

const Slider = ({ open, openRight, children, fullWidth }) => (
  <CSSTransition
    in={open}
    // Wait for animation to finish before unmount.
    timeout={{ enter: 0, exit: EXIT_ANIMATION }}
    classNames="slider"
    unmountOnExit
  >
    <div className={classNames('slider', { openRight }, { fullWidth })}>{children}</div>
  </CSSTransition>
);

Slider.propTypes = {
  open: Types.bool,
  openRight: Types.bool,
  children: Types.node,
  fullWidth: Types.bool,
};

Slider.defaultProps = {
  open: false,
  openRight: false,
  children: null,
  fullWidth: false,
};

export default Slider;



